<template>
	<div class="menu">
			<ul>
				<li >
						<label for="ipt_1">起点</label><input type="text" ip="ipt_1" v-model="start">
				</li>
				<li v-for="(item,ind) in arr">
						<label for="ipt_arr">途径{{ind+1}}</label><input type="text"  v-model="arr[ind]"><button @click="del(ind)">删除</button>
				</li>
				<li >
						<label for="ipt_2">终点</label><input type="text" id="ipt_2" v-model="end"><button @click="add">添加</button>
				</li>
			</ul>
	</div>
</template>
<script>
export default {
	name: 'menu',
	data () {
		return {
			msg: '菜单',
			start:"起点",
			end:'终点',
			arr:['途径1'],//途径
		}
	},
	created(){
		this.userLoginState()
	},
	methods:{
		add(){
			this.arr.push(this.end)
			this.end=this.arr.length+1
		},
		del(index){
			if(this.arr.length==1){return}
			this.arr.splice(index,1)
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
	.menu{
		font-size: 16px;
		ul{
			width: 500px;
			margin: 100px auto;
			li{
				margin-top: 10px;
				display: flex;
				label{
					flex:1;
					display: inline-block;
					min-width: 100px;
					max-width: 100px;
				}
				input{
					flex:1;
				}
			}
		}
	}
</style>



